<template>
  <div class="wrapper">
    <div class="clock">
      <p class="time">{{ time }}</p>
      <p class="week">{{ week }}</p>
      <p class="date">{{ date }}</p>

    </div>
  </div>
</template>

<script>
  export default {
    name: "index",
    data() {
      return {
        date: '',
        week: '',
        time: ''
      }
    },
    mounted() {
      this.init();
    },
    methods: {
      init() {
        let _this = this;
        _this.weeks = ['日', '一', '二', '三', '四', '五', '六'];
        let timerID = setInterval(_this.updateTime, 1000);
        _this.updateTime();
      },
      updateTime() {
        let cd = new Date();
        this.time = this.zeroPadding(cd.getHours(), 2) + ':' + this.zeroPadding(cd.getMinutes(), 2);// getSeconds()
        this.week = '星期' + this.weeks[cd.getDay()];
        this.date = this.zeroPadding(cd.getFullYear(), 4) + '/' + this.zeroPadding(cd.getMonth() + 1, 2) + '/' + this.zeroPadding(cd.getDate(), 2);
      },
      zeroPadding(num, digit) {
        let zero = '';
        for (let i = 0; i < digit; i++) {
          zero += '0';
        }
        return (zero + num).slice(-digit);
      }
    }
  }
</script>

<style lang="stylus" scoped>
