<template>
  <div class="date-slider">
    <el-date-picker
      v-model="dateValue"
      value-format="yyyy-MM-dd"
      type="daterange"
      range-separator="-"
      :start-placeholder="getPrevTime"
      :end-placeholder="getTime"
      popper-class="filter-date-pop"
      :picker-options="pickerOptions"
      @change="handleClick"
    ></el-date-picker>
  </div>
</template>

<script>
  export default {
    name: "index",
    data() {
      return {
        dateValue: '',
        //格式：2020-06-08
        startDate: '',
        endDate: '',
        pickerOptions: {
          shortcuts:
            [
              {
                text: '最近一周',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  picker.$emit('pick', [start, end]);
                }
              },
              {
                text: '最近一个月',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  picker.$emit('pick', [start, end]);
                }
              },
              {
                text: '最近三个月',
                onClick(picker) {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  picker.$emit('pick', [start, end]);
                }
              }
            ]
        },
      }
    },
    mounted(){
      this.$store.commit({
        type: 'setDate',
        date: {
          startDate:this.getPrevTime,
          endDate:this.getTime
        }
      });
      // 设置时间选择器面板默认值
      this.$nextTick(()=>{
        const end = new Date();
        const start = new Date();
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
        this.dateValue = [start,end];
      })
    },
    methods: {
      handleClick(date) {
        this.startDate = date[0];
        this.endDate = date[1];
        this.$store.commit({
          type: 'setDate',
          date: {
            startDate:this.startDate,
            endDate:this.endDate
          }
        });
      },
      // 日期填充0
      zeroPadding(num, digit) {
        let zero = '';
        for (let i = 0; i < digit; i++) {
          zero += '0';
        }
        return (zero + num).slice(-digit);
      },
    },
    computed: {
      // 获取当前日期
      getTime() {
        let cd = new Date();
        let date = this.zeroPadding(cd.getFullYear(), 4) + '-' + this.zeroPadding(cd.getMonth() + 1, 2) + '-' + this.zeroPadding(cd.getDate(), 2);
        return date
      },
      // 获取30天之前日期
      getPrevTime() {
        let cd = new Date();
        let lw = new Date(cd - 1000 * 60 * 60 * 24 * 30);//最后一个数字30代表30天
        let date = this.zeroPadding(lw.getFullYear(), 4) + "-" + this.zeroPadding(lw.getMonth() + 1, 2) + "-" + this.zeroPadding(lw.getDate(), 2);
        return date
      }
    }
  }
</script>

