  .date-slider
    position absolute
    top 0.1rem
    right -0.2rem
    width 382px
    height 57px
    z-index 1
    >>> .el-date-editor
      position absolute
      top 0
      left 0
    >>> .el-range-separator
      color #8df6ff
      font-size 0.5rem
      line-height 0.25rem
    >>> .el-range-input
      border none
      height 0.3rem
      line-height 0.3rem
</style>
