<!--
  @auth:lcw
  脉冲动效
-->
<template>
  <div class="wrapper" ref="pulse" :style="{width:width+'px'}">
    <div class="pulse-item clearfloat" :style="{width:width+'px',transform:skewDir?'skew(-45deg, 0)':'skew(45deg, 0)'}">
      <i class="item" v-for="(item,index) of itemArr" :style="{ opacity: skewDir?(itemIndex<=index?0.3:1):(itemIndex>index?0.3:1)}"></i>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: "index",
    props: {
      width: Number,
      skewDir: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        timer: null,
        flag: true,
        itemIndex: 0,
        itemArr: []
      }
    },
    mounted() {
      this.init();
    },
    methods: {
      init() {
        if (this.timer !== null) {
          clearInterval(this.timer);
        }

        let wrapper = $(this.$refs.pulse);
        let W = wrapper.find(".pulse-item").width(); //脉冲区域长度
        let itemW = 14;//li长度
        let num = Math.floor(W / itemW);//li数量

        // 创建item数组
        for (let i = 0; i < num; i++) {
          this.itemArr.push('');
        }

        if (this.skewDir) {
          this.flag = true;
          this.itemIndex = 0;
        } else {
          this.flag = false;
          this.itemIndex = num;
        }

        this.timer = setInterval(() => {
          if (this.flag) {
            this.itemIndex++;
            if (this.itemIndex === num) this.flag = false;
          } else {
            this.itemIndex--;
            if (this.itemIndex === 0) this.flag = true;
          }
        }, 300);
      }
    },
    beforeDestroy() {
      clearInterval(this.timer);
    }
  }
</script>

<style lang="stylus" scoped>
  .wrapper
    position absolute
    height 10px
    .pulse-item
      position absolute
      top 0
      left 0
      //transform skew(22deg, 0)
      .item
        float left
        margin 0 2px
        display block
        width 10px
        height 14px
        background-color #00b0ff
        opacity 1

</style>
