        pieMoreColors: ["#13f0ff", "#4eed99", "#0e95d2", "#52c7ff", "#4effe0", "#ccbe51", "#cc9c50", "#e3714d", "#e14974", "#a155e1", "#5569e1", "#eb837c", "#ea038b", "#0024ff"],
        pieMiddleColors: ["#ff7a73", "#f89e67", "#13f0ff", "#4eed99", "#d93e75", "#ccbe51", "#eb9002", "#1ba1ff"],

