<template>
  <div ref="yearSliderWrapper" class="sliderWrapper">
    <div class="yearSlider">
      <ul class="sliderInner clearfloat" ref="sliderInner"
          :style="{width: sliderInnerW,left:sliderInnerLeft}"

          @mousedown.stop.prevent="sliderStart"
          @touchstart.prevent="sliderStart"

          @mousemove.stop.prevent="sliderMove"
          @touchmove="sliderMove"

          @mouseup.stop.prevent="sliderEnd"
          @touchend="sliderEnd"

      >
        <li class="yearItem" v-for="(item,index) of yearArr" :key="index">{{item}}</li>
      </ul>
    </div>
  </div>

</template>

<script>
  import $ from 'jquery'
  export default {
    name: "index",
    props: {
      id: {
        type: String,
        default() {
          return 'yearSlider'
        }
      }
    },
    data() {
      return {
        yearWidth: 75,//单个年份宽度
        yearArr: [2016, 2017, 2018, 2019, 2020, 2021, 2022, 2023],//年份数组
        touchStatus: false,//滑动状态
        startX: 0,//拖拽起始X值
        moveX: 0,
        offsetX: 0,
        nowLeft: 0
      }
    },
    mounted() {

      this.wrapper = $(this.$refs.yearSliderWrapper);

      let nowIndex = Math.floor(this.yearArr.length / 2);
      let nowYear = this.yearArr[nowIndex];
      let indexDiff = this.year - nowYear; //年份序号差
      this.nowLeft = -Math.floor(this.yearArr.length / 2 - 2 + indexDiff) * this.yearWidth;//当前left值
      // 重新设置年份样式
      this.setYearStyle(nowIndex + indexDiff - 2);

    },
    methods: {
      sliderStart(event) {
        this.touchStatus = true;
        this.startX = event.clientX || event.changedTouches[0].clientX;
      },
      sliderMove(event) {
        if (this.touchStatus) {

          // 判断年份是否滑动出边界
          let _left = parseInt(this.sliderInnerLeft.substring(0, this.sliderInnerLeft.length - 2));

          if (Number(_left) > 140) {
            this.$refs.sliderInner.style.left = '140px';
            return
          }

          if (Number(_left) < -Math.ceil(this.yearArr.length / 2 + 1) * this.yearWidth) {
            this.$refs.sliderInner.style.left = -Math.ceil(this.yearArr.length / 2 + 1) * this.yearWidth + 'px';
            return
          }

          this.moveX = event.clientX || event.changedTouches[0].clientX;
          this.offsetX = this.moveX - this.startX; // 滑动偏移量

          let _nowNum = Math.round(_left / -this.yearWidth);

          this.setYearStyle(_nowNum);
        }
      },
      sliderEnd(event) {
        this.touchStatus = false;

        // 当前Left值
        this.offsetX = 0;
        this.nowLeft = Number(this.$refs.sliderInner.style.left.substring(0, this.$refs.sliderInner.style.left.length - 2));

        let nowIndex = this.nowLeft / -this.yearWidth;
        let _minRemainder = Math.floor(nowIndex);
        let _maxRemainder = Math.ceil(nowIndex);
        let _endNum;

        if (nowIndex >= 0) {
          _endNum = Math.abs(_maxRemainder - nowIndex) <= Math.abs(nowIndex - _minRemainder) ? _maxRemainder : _minRemainder;
        }
        else {
          _endNum = Math.abs(nowIndex - _maxRemainder) <= Math.abs(_minRemainder - nowIndex) ? _maxRemainder : _minRemainder;
        }

        this.$refs.sliderInner.style.left = (_endNum * -this.yearWidth) + "px";

        this.setYearStyle(_endNum);

        this.$emit('change', this.yearArr[_endNum + 2]);
      },
      setYearStyle(index) {
        let yearItem = this.wrapper.find('.yearItem');
        yearItem.css({'color': 'transparent', 'font-size': '13px'});
        yearItem.eq(index + 1).css({'color': '#70a5b2', 'font-size': '15px'});
        yearItem.eq(index + 3).css({'color': '#70a5b2', 'font-size': '15px'});
        yearItem.eq(index + 2).css({'color': '#d73e2d', 'font-size': '18px', 'font-weight': "bold"});
      }
    },
    computed: {
      // 滑动区域宽度
      sliderInnerW() {
        return this.yearArr.length * this.yearWidth + 'px'
      },
      // 滑动left值
      sliderInnerLeft() {
        return this.nowLeft + this.offsetX + 'px'
      },
      year() {
        return this.$store.state.year
      }
    },
    watch: {
      year(val) {
        let nowIndex = Math.floor(this.yearArr.length / 2);
        let nowYear = this.yearArr[nowIndex];
        let indexDiff = val - nowYear; //年份序号差
        this.nowLeft = -Math.floor(this.yearArr.length / 2 - 2 + indexDiff) * this.yearWidth;//当前left值，sliderInnerLeft自动更新
        // 重新设置年份样式
        this.setYearStyle(nowIndex + indexDiff - 2);
      }
    },
  }
</script>

<style lang="stylus" scoped>
  .sliderWrapper
    position absolute
    top -0.1rem
    right 0
    width 382px
    height 57px
    z-index 1
    display inline-block
    vertical-align bottom
    .yearSlider
      position absolute
      top 10px
      left 16px
      width 375px
      height 57px
      overflow hidden
      background url("./img/yearSliderBg.png") no-repeat
      background-size 100% 100%
      ul.sliderInner
        position absolute
        top 12px
        list-style none
        li.yearItem
          float left
          width 75px
          line-height 37px
          text-align center
          color #033b51
          font-size 13px
          cursor pointer
</style>
